.posts {
  flex: 9;
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
  align-items: center;
  /* background-color: #444444; */
}

.post {
  width: 385px;
  /* margin: 2px 0px 40px 90px; */
  margin: auto;
  /* padding-left: 90px; */
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-color: Tomato; */
}

.post-image {
  width: 385px;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.post-tag {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  color: var(--color-post-tag);
  cursor: pointer;
  text-align: center;

  font-weight: 400;
  font-size: 11px;
  line-height: 19px;
  margin-top: 10px;
}

.post-title {
  font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 900;
  margin-top: 15px;
  cursor: pointer;
}

.post-date {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  font-weight: 400;
  color: #999999;
  margin-top: 15px;
}

.post-content {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #444444;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 11px;
  color: #be9656;
  line-height: 19px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}
